// History service for managing generation history
const HISTORY_KEY = 'ugc_generation_history';
const MAX_HISTORY_ITEMS = 50; // Keep last 50 generations

class HistoryService {
  constructor() {
    this.history = this.loadHistory();
  }

  loadHistory() {
    try {
      const stored = localStorage.getItem(HISTORY_KEY);
      return stored ? JSON.parse(stored) : [];
    } catch (error) {
      console.error('Error loading history:', error);
      return [];
    }
  }

  saveHistory() {
    try {
      localStorage.setItem(HISTORY_KEY, JSON.stringify(this.history));
    } catch (error) {
      console.error('Error saving history:', error);
    }
  }

  addGeneration(result) {
    const historyItem = {
      id: Date.now().toString(),
      timestamp: new Date().toISOString(),
      mode: result.settings?.continuationMode ? 'continuation' : 
            result.settings?.language === 'es' ? 'spanish' :
            result.settings?.jsonFormat === 'enhanced' ? 'enhanced' : 'standard',
      script: result.settings?.script || '',
      product: result.settings?.product || '',
      segments: result.segments || [],
      metadata: result.metadata || {},
      voiceProfile: result.voiceProfile || null,
      settings: result.settings || {}
    };

    // Add to beginning of array (most recent first)
    this.history.unshift(historyItem);

    // Keep only the most recent items
    if (this.history.length > MAX_HISTORY_ITEMS) {
      this.history = this.history.slice(0, MAX_HISTORY_ITEMS);
    }

    this.saveHistory();
    return historyItem;
  }

  getHistory() {
    return this.history;
  }

  getGenerationById(id) {
    return this.history.find(item => item.id === id);
  }

  deleteGeneration(id) {
    this.history = this.history.filter(item => item.id !== id);
    this.saveHistory();
  }

  clearHistory() {
    this.history = [];
    this.saveHistory();
  }

  exportHistory() {
    const dataStr = JSON.stringify(this.history, null, 2);
    const dataBlob = new Blob([dataStr], { type: 'application/json' });
    const url = URL.createObjectURL(dataBlob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `ugc-history-${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  }
}

export default new HistoryService();

