import React, { useState, useEffect } from 'react';

function VideoDownloader({ videos, onDownloadComplete }) {
  const [videoStatuses, setVideoStatuses] = useState({});
  const [downloading, setDownloading] = useState(false);

  useEffect(() => {
    // Initialize video statuses
    const initialStatuses = {};
    videos.forEach(video => {
      initialStatuses[video.videoId] = {
        status: video.status || 'processing',
        progress: 0,
        downloadUrl: null
      };
    });
    setVideoStatuses(initialStatuses);

    // Start polling for video status updates
    const pollInterval = setInterval(() => {
      pollVideoStatuses();
    }, 5000); // Poll every 5 seconds

    return () => clearInterval(pollInterval);
  }, [videos]);

  const pollVideoStatuses = async () => {
    const updates = {};
    
    for (const video of videos) {
      if (video.videoId && videoStatuses[video.videoId]?.status === 'processing') {
        try {
          const response = await fetch(`/api/video-status/${video.videoId}`);
          const data = await response.json();
          
          if (data.success) {
            updates[video.videoId] = {
              status: data.status,
              progress: data.progress || 0,
              downloadUrl: data.downloadUrl
            };
          }
        } catch (error) {
          console.error('Error polling video status:', error);
        }
      }
    }

    if (Object.keys(updates).length > 0) {
      setVideoStatuses(prev => ({
        ...prev,
        ...updates
      }));
    }
  };

  const handleDownloadVideo = async (videoId, segmentIndex) => {
    setDownloading(true);
    
    try {
      const response = await fetch(`/api/download-video/${videoId}`);
      
      if (response.ok) {
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `segment_${segmentIndex}.mp4`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(url);
        
        if (onDownloadComplete) {
          onDownloadComplete(videoId);
        }
      } else {
        throw new Error('Download failed');
      }
    } catch (error) {
      console.error('Error downloading video:', error);
      alert('Error downloading video. Please try again.');
    } finally {
      setDownloading(false);
    }
  };

  const downloadAllVideos = async () => {
    setDownloading(true);
    
    for (const video of videos) {
      if (video.videoId && videoStatuses[video.videoId]?.status === 'completed') {
        await handleDownloadVideo(video.videoId, video.segmentIndex);
        // Add delay between downloads to avoid overwhelming the server
        await new Promise(resolve => setTimeout(resolve, 1000));
      }
    }
    
    setDownloading(false);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'completed': return '#28a745';
      case 'processing': return '#ffc107';
      case 'error': return '#dc3545';
      default: return '#6c757d';
    }
  };

  const getStatusText = (status) => {
    switch (status) {
      case 'completed': return 'Listo para descargar';
      case 'processing': return 'Procesando...';
      case 'error': return 'Error';
      default: return 'Desconocido';
    }
  };

  const completedVideos = videos.filter(video => 
    video.videoId && videoStatuses[video.videoId]?.status === 'completed'
  );

  return (
    <div className="video-downloader">
      <div className="video-downloader-header">
        <h3>Videos Generados</h3>
        <div className="video-stats">
          <span>{completedVideos.length} de {videos.length} videos listos</span>
          {completedVideos.length > 0 && (
            <button 
              className="download-all-button"
              onClick={downloadAllVideos}
              disabled={downloading}
            >
              {downloading ? 'Descargando...' : 'Descargar Todos'}
            </button>
          )}
        </div>
      </div>

      <div className="video-list">
        {videos.map((video, index) => {
          const status = videoStatuses[video.videoId] || { status: 'processing', progress: 0 };
          
          return (
            <div key={index} className="video-item">
              <div className="video-info">
                <div className="video-segment">
                  Segmento {video.segmentIndex}
                </div>
                <div className="video-status">
                  <span 
                    className={`status-badge ${status.status}`}
                  >
                    {getStatusText(status.status)}
                  </span>
                  {status.status === 'processing' && (
                    <div className="progress-container">
                      <div className="progress-bar">
                        <div 
                          className="progress-fill" 
                          style={{ width: `${status.progress}%` }}
                        ></div>
                      </div>
                      <span className="progress-text">
                        {status.progress}% - Procesando...
                      </span>
                    </div>
                  )}
                </div>
              </div>
              
              <div className="video-actions">
                {status.status === 'completed' && (
                  <button
                    className="download-button"
                    onClick={() => handleDownloadVideo(video.videoId, video.segmentIndex)}
                    disabled={downloading}
                  >
                    Descargar
                  </button>
                )}
                {status.status === 'processing' && (
                  <div className="processing-indicator">
                    <div className="spinner"></div>
                  </div>
                )}
                {status.status === 'error' && (
                  <span className="error-text">Error en generacin</span>
                )}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default VideoDownloader;

