import React, { useState } from 'react';
import { generateSegments } from '../api/client';
import DownloadButton from './DownloadButton';
import ResultsDisplay from './ResultsDisplay';
import ProgressTracker from './ProgressTracker';
import VideoDownloader from './VideoDownloader';
import historyService from '../services/historyService';

function SpanishMode() {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [results, setResults] = useState(null);
  const [progress, setProgress] = useState({
    currentStep: 0,
    totalSteps: 7,
    estimatedTime: 0,
    status: '',
    segments: []
  });
  const [videos, setVideos] = useState([]);
  const [generatingVideos, setGeneratingVideos] = useState(false);

  const [formData, setFormData] = useState({
    script: '',
    ageRange: '25-34',
    gender: 'female',
    product: '',
    voiceType: 'warm-friendly',
    energyLevel: '80',
    jsonFormat: 'enhanced',
    settingMode: 'single',
    room: 'living room',
    style: 'casual and friendly',
    locations: [],
    cameraStyle: 'static-handheld',
    timeOfDay: 'morning',
    backgroundLife: false,
    productStyle: 'natural',
    energyArc: 'consistent',
    narrativeStyle: 'direct-review',
    ethnicity: 'hispanic-latino',
    characterFeatures: '',
    clothingDetails: '',
    accentRegion: 'es-MX',
    language: 'es',
    continuationMode: true,
    // Advanced settings
    avatarMode: 'human',
    animal: {
      species: 'dog',
      anthropomorphic: false,
      voiceStyle: 'narrator'
    },
    generateVideos: false,
    videoQuality: 'standard'
  });

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData({
      ...formData,
      [name]: type === 'checkbox' ? checked : value
    });
  };

  const handleSettingModeChange = (mode) => {
    setFormData({
      ...formData,
      settingMode: mode,
      locations: mode === 'single' ? [] : getDefaultLocations(mode)
    });
  };

  const getDefaultLocations = (mode) => {
    switch (mode) {
      case 'home-tour':
        return ['living room', 'kitchen', 'bedroom', 'bathroom'];
      case 'indoor-outdoor':
        return ['living room', 'porch', 'backyard', 'kitchen'];
      default:
        return [];
    }
  };

  const handleLocationChange = (index, value) => {
    const newLocations = [...formData.locations];
    newLocations[index] = value;
    setFormData({ ...formData, locations: newLocations });
  };

  const handleAnimalChange = (field, value) => {
    setFormData({
      ...formData,
      animal: {
        ...formData.animal,
        [field]: value
      }
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError(null);
    setResults(null);
    
    // Initialize progress
    setProgress({
      currentStep: 1,
      totalSteps: 7,
      estimatedTime: 300, // 5 minutes estimate
      status: 'Analizando guion...',
      segments: []
    });

    // Phase 1: Script Generation Progress
    const scriptProgressSteps = [
      { step: 1, status: 'Analizando guion...', delay: 1000 },
      { step: 2, status: 'Generando descripciones base...', delay: 2000 },
      { step: 3, status: 'Creando segmentos individuales...', delay: 3000 },
      { step: 4, status: 'Generando perfiles de voz...', delay: 4000 },
      { step: 5, status: 'Procesando continuidad...', delay: 5000 }
    ];

    // Start script generation progress
    scriptProgressSteps.forEach(({ step, status, delay }) => {
      setTimeout(() => {
        setProgress(prev => ({
          ...prev,
          currentStep: step,
          status: status
        }));
      }, delay);
    });

    try {
      const response = await generateSegments({ ...formData });
      console.log('SpanishMode: API Response:', response);
      const result = { ...response, settings: formData };
      console.log('SpanishMode: Final result:', result);
      setResults(result);
      
      // Save to history
      historyService.addGeneration(result);
      
      // Update progress to complete
      setProgress(prev => ({
        ...prev,
        currentStep: 7,
        status: 'Generacin completada',
        segments: result.segments || []
      }));

      // Phase 2: Video Generation Progress
      if (formData.generateVideos) {
        setGeneratingVideos(true);
        
        // Update progress for video generation phase
        setProgress(prev => ({
          ...prev,
          currentStep: 6,
          status: 'Iniciando generacin de videos...',
          totalSteps: 7
        }));

        try {
          const videoResponse = await fetch('/api/generate-videos-veo3', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              segments: result.segments,
              videoQuality: formData.videoQuality,
              language: formData.language,
              sequential: true // Generate videos in sequence for continuity
            })
          });
          
          const videoData = await videoResponse.json();
          if (videoData.success) {
            setVideos(videoData.videos);
            
            // Start video generation progress tracking
            const videoProgressSteps = [
              { step: 6, status: 'Generando video 1...', delay: 2000 },
              { step: 6, status: 'Generando video 2...', delay: 4000 },
              { step: 6, status: 'Generando video 3...', delay: 6000 },
              { step: 6, status: 'Generando video 4...', delay: 8000 },
              { step: 6, status: 'Generando video 5...', delay: 10000 },
              { step: 7, status: 'Finalizando videos...', delay: 12000 }
            ];

            videoProgressSteps.forEach(({ step, status, delay }) => {
              setTimeout(() => {
                setProgress(prev => ({
                  ...prev,
                  currentStep: step,
                  status: status
                }));
              }, delay);
            });
          }
        } catch (videoErr) {
          console.error('Error generating videos:', videoErr);
        } finally {
          setGeneratingVideos(false);
        }
      }
      
    } catch (err) {
      setError(err.message || 'Something went wrong');
    } finally {
      setLoading(false);
    }
  };

  // Debug logging
  console.log('SpanishMode render - results:', results);
  console.log('SpanishMode render - loading:', loading);
  console.log('SpanishMode render - error:', error);

  return (
    <div className="continuation-mode-container">
      <div className="continuation-header">
        <h2>Spanish Mode</h2>
        <p className="section-description">
          Genera segmentos de video en espaol con continuidad de voz y comportamiento. Ajusta acento regional en "Accent/Regional Voice".
        </p>
      </div>

      {loading && (
        <ProgressTracker 
          progress={progress}
          currentStep={progress.currentStep}
          totalSteps={progress.totalSteps}
          estimatedTime={progress.estimatedTime}
          status={progress.status}
        />
      )}

      {!results ? (
        <form onSubmit={handleSubmit} className="form-container">
          {/* Basic Character Information */}
          <div className="form-section">
            <h3>Informacin del Personaje</h3>
            <div className="form-row">
              <div className="form-group">
                <label htmlFor="ageRange">Rango de Edad *</label>
                <select id="ageRange" name="ageRange" value={formData.ageRange} onChange={handleChange} required>
                  <option value="18-24">18-24</option>
                  <option value="25-34">25-34</option>
                  <option value="35-44">35-44</option>
                  <option value="45-54">45-54</option>
                  <option value="55+">55+</option>
                </select>
              </div>
              <div className="form-group">
                <label htmlFor="gender">Gnero *</label>
                <select id="gender" name="gender" value={formData.gender} onChange={handleChange} required>
                  <option value="female">Femenino</option>
                  <option value="male">Masculino</option>
                  <option value="non-binary">No binario</option>
                </select>
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <label htmlFor="voiceType">Tipo de Voz</label>
                <select id="voiceType" name="voiceType" value={formData.voiceType} onChange={handleChange}>
                  <option value="warm-friendly">Clida y Amigable</option>
                  <option value="professional-clear">Profesional y Clara</option>
                  <option value="energetic-upbeat">Energtica</option>
                  <option value="calm-soothing">Calma</option>
                  <option value="conversational-casual">Conversacional</option>
                </select>
              </div>
              <div className="form-group">
                <label htmlFor="energyLevel">Nivel de Energa (%)</label>
                <input type="number" id="energyLevel" name="energyLevel" value={formData.energyLevel} onChange={handleChange} min="50" max="100" step="5" />
              </div>
            </div>
          </div>

          {/* Avatar Mode Selection */}
          <div className="form-section">
            <h3>Tipo de Avatar</h3>
            <div className="form-group">
              <label>Modo de Avatar</label>
              <div className="radio-group">
                <label>
                  <input
                    type="radio"
                    name="avatarMode"
                    value="human"
                    checked={formData.avatarMode === 'human'}
                    onChange={handleChange}
                  />
                  Humano
                </label>
                <label>
                  <input
                    type="radio"
                    name="avatarMode"
                    value="animal"
                    checked={formData.avatarMode === 'animal'}
                    onChange={handleChange}
                  />
                  Animal
                </label>
              </div>
            </div>

            {formData.avatarMode === 'animal' && (
              <div className="form-row">
                <div className="form-group">
                  <label htmlFor="animalSpecies">Especie</label>
                  <select 
                    id="animalSpecies" 
                    value={formData.animal.species} 
                    onChange={(e) => handleAnimalChange('species', e.target.value)}
                  >
                    <option value="dog">Perro</option>
                    <option value="cat">Gato</option>
                    <option value="bird">Pjaro</option>
                    <option value="rabbit">Conejo</option>
                    <option value="hamster">Hmster</option>
                  </select>
                </div>
                <div className="form-group">
                  <label htmlFor="animalVoiceStyle">Estilo de Voz</label>
                  <select 
                    id="animalVoiceStyle" 
                    value={formData.animal.voiceStyle} 
                    onChange={(e) => handleAnimalChange('voiceStyle', e.target.value)}
                  >
                    <option value="narrator">Narrador</option>
                    <option value="character">Personaje</option>
                  </select>
                </div>
              </div>
            )}
          </div>

          {/* Advanced Character Details */}
          <div className="form-section">
            <h3>Detalles Avanzados del Personaje</h3>
            <div className="form-row">
              <div className="form-group">
                <label htmlFor="ethnicity">Etnia/Apareciencia</label>
                <select id="ethnicity" name="ethnicity" value={formData.ethnicity || ''} onChange={handleChange}>
                  <option value="">No especificado</option>
                  <option value="caucasian">Caucsico</option>
                  <option value="african-american">Afroamericano</option>
                  <option value="hispanic-latino">Hispano/Latino</option>
                  <option value="asian-east">Asitico Oriental</option>
                  <option value="asian-south">Asitico del Sur</option>
                  <option value="middle-eastern">Medio Oriente</option>
                  <option value="mixed-race">Mestizo</option>
                  <option value="pacific-islander">Isleo del Pacfico</option>
                  <option value="native-american">Nativo Americano</option>
                </select>
              </div>
              <div className="form-group">
                <label htmlFor="accentRegion">Acento/Regin</label>
                <select id="accentRegion" name="accentRegion" value={formData.accentRegion || 'es-MX'} onChange={handleChange}>
                  <option value="es-ES">Espaa (es-ES)</option>
                  <option value="es-MX">Mxico (es-MX)</option>
                  <option value="es-AR">Argentina (es-AR)</option>
                  <option value="es-CO">Colombia (es-CO)</option>
                  <option value="es-CL">Chile (es-CL)</option>
                  <option value="es-PE">Per (es-PE)</option>
                  <option value="es-VE">Venezuela (es-VE)</option>
                  <option value="neutral">Neutral</option>
                </select>
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <label htmlFor="characterFeatures">Caractersticas Especficas (Opcional)</label>
                <input
                  type="text"
                  id="characterFeatures"
                  name="characterFeatures"
                  value={formData.characterFeatures || ''}
                  onChange={handleChange}
                  placeholder="ej: pelo rizado, gafas, pecas, barba..."
                />
              </div>
              <div className="form-group">
                <label htmlFor="clothingDetails">Detalles de Ropa (Opcional)</label>
                <input
                  type="text"
                  id="clothingDetails"
                  name="clothingDetails"
                  value={formData.clothingDetails || ''}
                  onChange={handleChange}
                  placeholder="ej: suter de lana crema, jeans azul oscuro, collar de plata..."
                />
              </div>
            </div>
          </div>

          {/* Product and Script */}
          <div className="form-section">
            <h3>Producto y Guion</h3>
            <div className="form-group">
              <label htmlFor="product">Nombre del Producto *</label>
              <input
                type="text"
                id="product"
                name="product"
                value={formData.product}
                onChange={handleChange}
                placeholder="ej: Suero Facial, Cafetera..."
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="script">Guion Completo *</label>
              <textarea
                id="script"
                name="script"
                value={formData.script}
                onChange={handleChange}
                placeholder="Ingresa tu guion completo de UGC aqu. Se dividir automticamente en segmentos de 8 segundos..."
                rows={8}
                required
              />
              <p className="form-help-text">
                Cada segmento necesita 15-22 palabras (6-8 segundos de habla). Las oraciones cortas se combinarn automticamente.
              </p>
            </div>
          </div>

          {/* Setting Configuration */}
          <div className="form-section">
            <h3>Configuracin de Escenario</h3>
            <div className="form-group">
              <label>Modo de Escenario</label>
              <div className="radio-group">
                <label>
                  <input
                    type="radio"
                    name="settingMode"
                    value="single"
                    checked={formData.settingMode === 'single'}
                    onChange={() => handleSettingModeChange('single')}
                  />
                  Ubicacin nica
                </label>
                <label>
                  <input
                    type="radio"
                    name="settingMode"
                    value="home-tour"
                    checked={formData.settingMode === 'home-tour'}
                    onChange={() => handleSettingModeChange('home-tour')}
                  />
                  Tour del Hogar
                </label>
                <label>
                  <input
                    type="radio"
                    name="settingMode"
                    value="indoor-outdoor"
                    checked={formData.settingMode === 'indoor-outdoor'}
                    onChange={() => handleSettingModeChange('indoor-outdoor')}
                  />
                  Interior/Exterior Mixto
                </label>
              </div>
            </div>

            {formData.settingMode === 'single' ? (
              <div className="form-group">
                <label htmlFor="room">Habitacin/Ubicacin *</label>
                <select id="room" name="room" value={formData.room} onChange={handleChange} required>
                  <option value="living room">Sala de estar</option>
                  <option value="kitchen">Cocina</option>
                  <option value="bedroom">Dormitorio</option>
                  <option value="bathroom">Bao</option>
                  <option value="home office">Oficina en casa</option>
                  <option value="dining room">Comedor</option>
                  <option value="porch">Porche</option>
                  <option value="backyard">Patio trasero</option>
                  <option value="garage">Garaje</option>
                  <option value="balcony">Balcn</option>
                </select>
              </div>
            ) : (
              <div className="form-group">
                <label>Secuencia de Ubicaciones</label>
                {formData.locations.map((location, index) => (
                  <div key={index} className="location-item">
                    <span>Segmento {index + 1}:</span>
                    <select
                      value={location}
                      onChange={(e) => handleLocationChange(index, e.target.value)}
                    >
                      <option value="living room">Sala de estar</option>
                      <option value="kitchen">Cocina</option>
                      <option value="bedroom">Dormitorio</option>
                      <option value="bathroom">Bao</option>
                      <option value="home office">Oficina en casa</option>
                      <option value="dining room">Comedor</option>
                      <option value="porch">Porche</option>
                      <option value="backyard">Patio trasero</option>
                      <option value="garage">Garaje</option>
                      <option value="balcony">Balcn</option>
                    </select>
                  </div>
                ))}
                <p className="form-help-text">
                  Las ubicaciones se asignarn a los segmentos en orden. Los segmentos extra usarn la ltima ubicacin.
                </p>
              </div>
            )}

            <div className="form-group">
              <label htmlFor="style">Estilo del Personaje *</label>
              <select id="style" name="style" value={formData.style} onChange={handleChange} required>
                <option value="casual and friendly">Casual y Amigable</option>
                <option value="polished professional">Profesional Pulido</option>
                <option value="authentic everyday">Autntico Cotidiano</option>
                <option value="enthusiastic influencer">Influencer Entusiasta</option>
                <option value="calm expert">Experto Calmado</option>
              </select>
            </div>
          </div>

          {/* Visual Production Settings */}
          <div className="form-section">
            <h3>Configuracin de Produccin Visual</h3>
            <div className="form-row">
              <div className="form-group">
                <label htmlFor="cameraStyle">Estilo de Cmara</label>
                <select id="cameraStyle" name="cameraStyle" value={formData.cameraStyle} onChange={handleChange}>
                  <option value="static-handheld">Esttica de Mano</option>
                  <option value="smooth-movement">Movimiento Suave</option>
                  <option value="dynamic-cuts">Cortes Dinmicos</option>
                  <option value="documentary-style">Estilo Documental</option>
                  <option value="pov-selfie">POV Selfie (telfono en mano)</option>
                </select>
              </div>

              <div className="form-group">
                <label htmlFor="timeOfDay">Hora del Da</label>
                <select id="timeOfDay" name="timeOfDay" value={formData.timeOfDay} onChange={handleChange}>
                  <option value="morning">Maana</option>
                  <option value="afternoon">Tarde</option>
                  <option value="golden-hour">Hora Dorada</option>
                  <option value="evening">Noche</option>
                </select>
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <label htmlFor="productStyle">Estilo de Exhibicin del Producto</label>
                <select id="productStyle" name="productStyle" value={formData.productStyle} onChange={handleChange}>
                  <option value="natural">Manejo Natural</option>
                  <option value="hero-shots">Tomas Heroicas</option>
                  <option value="lifestyle-integrated">Integrado al Estilo de Vida</option>
                  <option value="demonstration-focused">Enfoque en Demostracin</option>
                </select>
              </div>

              <div className="form-group">
                <label htmlFor="energyArc">Arco de Energa</label>
                <select id="energyArc" name="energyArc" value={formData.energyArc} onChange={handleChange}>
                  <option value="consistent">Consistente Durante Todo</option>
                  <option value="building">Construyendo Emocin</option>
                  <option value="problem-solution">Problema  Solucin</option>
                  <option value="discovery">Viaje de Descubrimiento</option>
                </select>
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <label htmlFor="narrativeStyle">Estilo Narrativo</label>
                <select id="narrativeStyle" name="narrativeStyle" value={formData.narrativeStyle} onChange={handleChange}>
                  <option value="direct-review">Resea Directa</option>
                  <option value="storytelling">Narrativa</option>
                  <option value="educational">Educativo</option>
                  <option value="testimonial">Testimonial</option>
                </select>
              </div>

              <div className="form-group checkbox-group">
                <label>
                  <input
                    type="checkbox"
                    name="backgroundLife"
                    checked={formData.backgroundLife}
                    onChange={handleChange}
                  />
                  Incluir Vida de Fondo
                </label>
                <p className="form-help-text">
                  Aadir actividad sutil de fondo para realismo
                </p>
              </div>
            </div>
          </div>

          {/* Output Format and Video Generation */}
          <div className="form-section">
            <h3>Formato de Salida y Generacin de Video</h3>
            <div className="form-row">
              <div className="form-group">
                <label htmlFor="jsonFormat">Formato JSON</label>
                <select id="jsonFormat" name="jsonFormat" value={formData.jsonFormat} onChange={handleChange}>
                  <option value="standard">Estndar (Detalle balanceado)</option>
                  <option value="enhanced">Mejorado (Mximo detalle de continuidad)</option>
                </select>
                <p className="form-help-text">
                  Ambos formatos tendrn secciones mejoradas de voz/comportamiento en modo espaol
                </p>
              </div>

              <div className="form-group">
                <label htmlFor="videoQuality">Calidad de Video</label>
                <select id="videoQuality" name="videoQuality" value={formData.videoQuality} onChange={handleChange}>
                  <option value="standard">Estndar (HD)</option>
                  <option value="high">Alta (4K)</option>
                </select>
              </div>
            </div>

            <div className="form-group checkbox-group">
              <label>
                <input
                  type="checkbox"
                  name="generateVideos"
                  checked={formData.generateVideos}
                  onChange={handleChange}
                />
                Generar Videos Automticamente
              </label>
              <p className="form-help-text">
                Generar videos usando Veo 3 API despus de crear los segmentos JSON
              </p>
            </div>
          </div>

          <button type="submit" className="submit-button" disabled={loading}>
            {loading ? 'Generando Segmentos en Espaol...' : 'Generar Segmentos en Espaol'}
          </button>
        </form>
      ) : (
        <>
          <ResultsDisplay results={results} />
          <DownloadButton segments={results.segments} metadata={results.metadata} />
          
          {generatingVideos && (
            <div className="video-generation-status">
              <h3>Generando Videos...</h3>
              <p>Los videos se estn generando usando Veo 3. Esto puede tomar varios minutos.</p>
            </div>
          )}
          
          {videos.length > 0 && (
            <VideoDownloader 
              videos={videos} 
              onDownloadComplete={(videoId) => {
                console.log('Video downloaded:', videoId);
              }}
            />
          )}
          
          <button onClick={() => setResults(null)} className="back-button">Generar Nuevo</button>
        </>
      )}

      {error && <div className="error-message">Error: {error}</div>}
    </div>
  );
}

export default SpanishMode;


