import React, { useState, useEffect } from 'react';
import historyService from '../services/historyService';
import DownloadButton from './DownloadButton';
import ResultsDisplay from './ResultsDisplay';
import VideoDownloader from './VideoDownloader';
import JSONEditor from './JSONEditor';

function HistoryDisplay() {
  const [history, setHistory] = useState([]);
  const [selectedGeneration, setSelectedGeneration] = useState(null);
  const [showHistory, setShowHistory] = useState(false);
  const [showJSON, setShowJSON] = useState(false);
  const [videoStatuses, setVideoStatuses] = useState({});
  const [generatingVideos, setGeneratingVideos] = useState(false);

  useEffect(() => {
    setHistory(historyService.getHistory());
  }, []);

  const formatDate = (timestamp) => {
    return new Date(timestamp).toLocaleString();
  };

  const getModeLabel = (mode) => {
    const labels = {
      'standard': 'Standard',
      'continuation': 'Continuation',
      'spanish': 'Spanish',
      'enhanced': 'Enhanced'
    };
    return labels[mode] || mode;
  };

  const getModeColor = (mode) => {
    const colors = {
      'standard': '#007bff',
      'continuation': '#28a745',
      'spanish': '#ffc107',
      'enhanced': '#6f42c1'
    };
    return colors[mode] || '#6c757d';
  };

  const handleViewGeneration = (generation) => {
    setSelectedGeneration(generation);
    setShowHistory(false);
  };

  const handleDeleteGeneration = (id) => {
    if (window.confirm('Are you sure you want to delete this generation?')) {
      historyService.deleteGeneration(id);
      setHistory(historyService.getHistory());
      if (selectedGeneration && selectedGeneration.id === id) {
        setSelectedGeneration(null);
      }
    }
  };

  const handleExportHistory = () => {
    historyService.exportHistory();
  };

  const handleClearHistory = () => {
    if (window.confirm('Are you sure you want to clear all history? This cannot be undone.')) {
      historyService.clearHistory();
      setHistory([]);
      setSelectedGeneration(null);
    }
  };

  const handleGenerateVideos = async (generation) => {
    setGeneratingVideos(true);
    try {
      const response = await fetch('/api/generate-videos-veo3', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          segments: generation.segments,
          videoQuality: 'standard',
          language: generation.settings?.language || 'es'
        })
      });
      const data = await response.json();
      if (data.success) {
        setVideoStatuses(prev => ({
          ...prev,
          [generation.id]: data.videos
        }));
      }
    } catch (error) {
      console.error('Error generating videos:', error);
    } finally {
      setGeneratingVideos(false);
    }
  };

  const pollVideoStatuses = async (generationId) => {
    const videos = videoStatuses[generationId];
    if (!videos) return;

    for (const video of videos) {
      if (video.videoId && video.status === 'processing') {
        try {
          const response = await fetch(`/api/video-status/${video.videoId}`);
          const data = await response.json();
          if (data.success) {
            setVideoStatuses(prev => ({
              ...prev,
              [generationId]: prev[generationId].map(v => 
                v.videoId === video.videoId ? { ...v, ...data } : v
              )
            }));
          }
        } catch (error) {
          console.error('Error polling video status:', error);
        }
      }
    }
  };

  // Poll video statuses every 5 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      Object.keys(videoStatuses).forEach(generationId => {
        pollVideoStatuses(generationId);
      });
    }, 5000);

    return () => clearInterval(interval);
  }, [videoStatuses]);

  if (selectedGeneration) {
    const currentVideos = videoStatuses[selectedGeneration.id] || [];
    const hasVideos = currentVideos.length > 0;
    const allVideosCompleted = currentVideos.length > 0 && currentVideos.every(video => video.status === 'completed');
    
    return (
      <div className="history-display">
        <div className="history-header">
          <button 
            className="back-button"
            onClick={() => setSelectedGeneration(null)}
          >
             Back to History
          </button>
          <h3>Generation from {formatDate(selectedGeneration.timestamp)}</h3>
          <div className="generation-info">
            <span 
              className="mode-badge"
              style={{ backgroundColor: getModeColor(selectedGeneration.mode) }}
            >
              {getModeLabel(selectedGeneration.mode)}
            </span>
            <span className="product-info">Product: {selectedGeneration.product}</span>
            <span className="segments-info">{selectedGeneration.segments.length} segments</span>
          </div>
        </div>
        
        <div className="generation-controls">
          <div className="view-controls">
            <button 
              className={`toggle-button ${!showJSON ? 'active' : ''}`}
              onClick={() => setShowJSON(false)}
            >
              Visual View
            </button>
            <button 
              className={`toggle-button ${showJSON ? 'active' : ''}`}
              onClick={() => setShowJSON(true)}
            >
              JSON View
            </button>
          </div>
          
          <div className="action-controls">
            {(!hasVideos || !allVideosCompleted) && (
              <button 
                className="generate-videos-button"
                onClick={() => handleGenerateVideos(selectedGeneration)}
                disabled={generatingVideos}
              >
                {generatingVideos ? 'Generating Videos...' : 'Generate Videos'}
              </button>
            )}
            <button 
              className="delete-button"
              onClick={() => handleDeleteGeneration(selectedGeneration.id)}
            >
              Delete This Generation
            </button>
          </div>
        </div>
        
        {showJSON ? (
          <div className="json-display">
            <h4>Full JSON Data</h4>
            <JSONEditor 
              data={selectedGeneration}
              readOnly={true}
            />
          </div>
        ) : (
          <>
            <ResultsDisplay 
              results={{
                segments: selectedGeneration.segments,
                metadata: selectedGeneration.metadata,
                voiceProfile: selectedGeneration.voiceProfile,
                settings: selectedGeneration.settings
              }} 
            />
            
            <DownloadButton 
              segments={selectedGeneration.segments} 
              metadata={selectedGeneration.metadata} 
            />
          </>
        )}
        
        {hasVideos && (
          <VideoDownloader 
            videos={currentVideos}
            onDownloadComplete={(videoId) => {
              console.log('Video downloaded:', videoId);
            }}
          />
        )}
      </div>
    );
  }

  return (
    <div className="history-display">
      <div className="history-header">
        <h2>Generation History</h2>
        <div className="history-controls">
          <button 
            className="export-button"
            onClick={handleExportHistory}
            disabled={history.length === 0}
          >
            Export History
          </button>
          <button 
            className="clear-button"
            onClick={handleClearHistory}
            disabled={history.length === 0}
          >
            Clear All
          </button>
        </div>
      </div>

      {history.length === 0 ? (
        <div className="no-history">
          <p>No generations found. Create your first script to see it here!</p>
        </div>
      ) : (
        <div className="history-list">
          {history.map((generation) => (
            <div key={generation.id} className="history-item">
              <div className="history-item-header">
                <div className="history-item-info">
                  <span 
                    className="mode-badge"
                    style={{ backgroundColor: getModeColor(generation.mode) }}
                  >
                    {getModeLabel(generation.mode)}
                  </span>
                  <span className="timestamp">{formatDate(generation.timestamp)}</span>
                </div>
                <div className="history-item-actions">
                  <button 
                    className="view-button"
                    onClick={() => handleViewGeneration(generation)}
                  >
                    View
                  </button>
                  <button 
                    className="delete-button"
                    onClick={() => handleDeleteGeneration(generation.id)}
                  >
                    Delete
                  </button>
                </div>
              </div>
              
              <div className="history-item-details">
                <div className="script-preview">
                  <strong>Script:</strong> {generation.scriptPreview || generation.script?.substring(0, 100) || 'No script preview'}
                  {(generation.scriptPreview || generation.script)?.length > 100 && '...'}
                </div>
                <div className="generation-stats">
                  <span><strong>Product:</strong> {generation.product}</span>
                  <span><strong>Segments:</strong> {generation.totalSegments || generation.segments?.length || 0}</span>
                  <span><strong>Duration:</strong> ~{generation.estimatedDuration || (generation.segments?.length * 8)}s</span>
                  {generation.voiceProfile && <span><strong>Voice Profile:</strong> Yes</span>}
                </div>
                {videoStatuses[generation.id] && (
                  <div className="video-status">
                    <strong>Videos:</strong> 
                    {videoStatuses[generation.id].filter(v => v.status === 'completed').length} / {videoStatuses[generation.id].length} completed
                    {videoStatuses[generation.id].some(v => v.status === 'processing') && (
                      <span className="processing-indicator"> (Processing...)</span>
                    )}
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default HistoryDisplay;

