import React from 'react';

function ProgressTracker({ progress, currentStep, totalSteps, estimatedTime, status }) {
  const percentage = totalSteps > 0 ? Math.round((currentStep / totalSteps) * 100) : 0;
  
  const formatTime = (seconds) => {
    if (seconds < 60) return `${seconds}s`;
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes}m ${remainingSeconds}s`;
  };

  const getStepDescription = (step, total) => {
    const steps = [
      'Analizando guion...',
      'Generando descripciones base...',
      'Creando segmentos individuales...',
      'Generando perfiles de voz...',
      'Procesando continuidad...',
      'Generando videos...',
      'Finalizando...'
    ];
    
    if (step <= steps.length) {
      return steps[step - 1] || `Paso ${step} de ${total}`;
    }
    return `Completado (${total} pasos)`;
  };

  return (
    <div className="progress-tracker">
      <div className="progress-header">
        <h3>Progreso de Generacin</h3>
        <div className="progress-stats">
          <span className="current-step">{getStepDescription(currentStep, totalSteps)}</span>
          <span className="time-estimate">
            {estimatedTime > 0 && `Tiempo estimado: ${formatTime(estimatedTime)}`}
          </span>
        </div>
      </div>
      
      <div className="progress-bar-container">
        <div className="progress-bar">
          <div 
            className="progress-fill"
            style={{ width: `${percentage}%` }}
          />
        </div>
        <div className="progress-text">
          {percentage}% completado
        </div>
      </div>
      
      <div className="progress-details">
        <div className="step-counter">
          Paso {currentStep} de {totalSteps}
        </div>
        {status && (
          <div className="status-message">
            {status}
          </div>
        )}
      </div>
      
      {progress.segments && progress.segments.length > 0 && (
        <div className="segments-preview">
          <h4>Segmentos Generados:</h4>
          <div className="segments-list">
            {progress.segments.map((segment, index) => (
              <div key={index} className="segment-item">
                <span className="segment-number">#{index + 1}</span>
                <span className="segment-text">
                  {segment.action_timeline?.dialogue?.substring(0, 50)}...
                </span>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}

export default ProgressTracker;

